#contact {
  justify-content: center;
  /* display: flex; */
  padding-top: 10%;
}
.contactDesc {
  padding: 1rem;
  font-size: medium;
  font-weight: 300;
  display: flex;
  justify-content: center;
}
.name,
.email,
.msg {
  font-size: medium;
  width: 100%;
  max-width: 40rem;
  margin: 0.5rem;
  padding: 0.5rem 1rem;
  color: white;
  border: none;
  border-radius: 0.5rem;
  background: rgb(40, 40, 40);
  display: flex;
}
.submitbtn {
  background: white;
  border: none;
  padding: 0.75rem 3.5rem;
  border-radius: 0.5rem;
  margin: 2rem;
}
.Contactform {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  /* width: 90vw; */
  /* max-width: 60rem;  */
  margin: 1rem;
}
.links {
  text-align: center;
}
.link {
  object-fit: cover;
  height: 3rem;
  width: 3rem;
  margin: 0 0.75rem;
  border-radius: 26rem;
}
@media (max-width: 768px) {
  #contact {
    padding-top: 15%;
  }
  .Contactform,
  input {
    justify-content: center;
    display: flow-root;
    margin-right: 26px;
    margin-bottom: 40px;
  }
  .submitbtn {
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    width: 100%;
  }
  .link {
    display: inline;
    /* justify-content: center; */
    /* border-radius: 26rem; */
    margin-top: 25px;
    /* margin-left: 10px; */
  }
  .contactDesc {
    text-align: justify;
  }
}
